<template>
<div class="headers">
  <span>胸痛中心管理平台</span>
  <ul class="loginInfoUl clearfix">
    <li>
      <img src="../../../static/img/user.png" style="display:inline-block;width:24px;height:24px;margin-top:6px;" alt="">
    </li>
    <li>
      您好
      <!--{{logindata.username}}-->
    </li>
    <li>
      {{util.logindata.userName}}
    </li>
    <li class="line">|</li>
    <li>
      <a style="color: white" @click="loginOut()">退出登录</a>
    </li>
  </ul>
</div>
</template>
<script>
import Vue from "vue";
export default{
    data(){
        return{}
    },
  methods:{
    loginOut(){
        var pThis = this;
      this.$router.push({name:'login'});
        this.$http.post(pThis.host('logout'))
          .then(function (res) {
            let datajson = res.data;
            let backjson={"state":datajson.state,"msg":datajson.msg};
//            pThis.backLogin(backjson);
            console.log(pThis.patientinfo.timer);
            clearInterval(this.location.timer);
            pThis.location.timer='';
            if(datajson.state =='1'){
//              var data=datajson.results[0];
//              this.$router.push({name:'login'});
            }
          }).catch(function (error) {
          console.log(error)
        });
      },
  }
}
</script>
<style lang="scss" scoped>
  .clearfix:after{display:block;content:"";clear:both;}
.headers {
  position:relative;
  font-family: "微软雅黑";
  height: 36px;
  line-height: 36px;
  background-color: #3a5ba4;
  color: #fff;
  font-size: 18px;
  /*padding-left: 20px;*/
  span{
    padding-left:20px;
  }
  .loginInfoUl{
    color:#fff;font-size:14px;position:absolute;right:18px;top:0;height:36px;line-height:36px;
    li{float:left;margin-left:10px;}
    .line{
      color:#193572;width:1px;
    }
  }
}
</style>



