<script >
import Vue from "vue";
import headerComponent from "@/components/header/topHeader";
import footerComponent from "@/components/footer/footer";
export default {
  name: "App",
  components: {
    headerComponent,
    footerComponent
  },
  data() {
    return {
      mainHeight:"100px",
      widths: 488,
      ajustHeight: "200px;",
      siderHeight: "",
      tipsTitle: "确定要取消任务吗？",
      noApply: false, //同意申请 置灰按钮

      car_Time: "", //发车时间
      arrival_Time: "", //到达时间
      firstContactMedical_Time: "", //首次接触医疗时间
      drawBlood_Time: "", //抽血时间
      firstTakeMedicine_Time: "" //首次吃药时间
    };
  },
  created() {
    var _that = this;
    _that.resizeClient(_that);
    $(window).resize(function() {
      _that.resizeClient(_that);
    });
    console.log('basePage')
  },
  mounted() {},
  computed: {
    menuitemClasses: function() {
      return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
    }
  },
  methods: {
    routerJump(name){
      this.util.menuActiveName=name;
    },
    resizeClient(that) {
      var that = this;
      // alert($('body').height())
      // alert($('body').height()-50)
      that.ajustHeight = $("body").height() -36 + "px";
      // $(".header").height() -
      // $(".footer").height() +
      // "px";
    },
    // 取消任务
    cancelPro() {
      this.$refs.cancelModal.openmodal();
      // this.$refs.tipmodal1.openmodal(); //点击取消项目打开弹框提示：是否要删除
    },
    agreeCancel() {
      // this.$refs.cancelModal.openmodal();
      this.$router.push({ path: "home" });
    },
    // cancel() {
    //   this.$refs.cancelModal.cancel();
    // },
    // 申请导管室
    apply() {
      this.$refs.agreeModal.showModal(); //点击取消项目打开弹框提示：是否要删除
    },
    ok() {
      this.$refs.agreeModal2.showModal();
    },
    cancel() {
      this.$refs.agreeModal2.cancel();
    },
    twiceOk() {
      // this.$refs.agreeModal2.openmodal();
      this.noApply = true;
    },
    getCarTime() {
      console.log(this.getTime().timeStampe);

      if (this.carTime == "") {
        this.car_Time = this.getTime().timeStampe.getTime();
        this.carTime = this.getTime().time;
        sessionStorage.setItem("car_Time", this.carTime);
        sessionStorage.getItem("car_Time");
      }
      $(".car").css("background-color", "green");
    },
    getArriveTime() {
      if (this.arrivalTime == "") {
        this.arrival_Time = this.getTime().timeStampe.getTime();
        this.arrivalTime = this.getTime().time;
        sessionStorage.setItem("arrival_Time", this.arrivalTime);
        sessionStorage.getItem("arrival_Time");
      }

      if (this.arrival_Time - this.car_Time < 600000) {
        $(".arrival").css("background-color", "green");
      } else {
        $(".arrival").css("background-color", "red");
      }
    },
    getContactMedicalTime() {
      if (this.firstContactMedicalTime == "") {
        this.firstContactMedical_Time = this.getTime().timeStampe.getTime();
        this.firstContactMedicalTime = this.getTime().time;
        sessionStorage.setItem(
          "firstContactMedical_Time",
          this.firstContactMedicalTime
        );
        sessionStorage.getItem("firstContactMedical_Time");
      }

      // if (this.firstContactMedicalTime == "") {
      //   this.firstContactMedicalTime = this.getTime();
      // }
      if (this.firstContactMedical_Time - this.arrival_Time <= 600000) {
        $(".ContactMedical").css("background-color", "green");
      } else {
        $(".ContactMedical").css("background-color", "red");
      }
    },
    getdrawBloodTime() {
      if (this.drawBloodTime == "") {
        this.drawBlood_Time = this.getTime().timeStampe.getTime();
        this.drawBloodTime = this.getTime().time;
        sessionStorage.setItem("drawBlood_Time", this.drawBloodTime);
        sessionStorage.getItem("drawBlood_Time");
      }
      if (this.drawBlood_Time - this.firstContactMedical_Time <= 600000) {
        $(".drawBlood").css("background-color", "green");
      } else {
        $(".drawBlood").css("background-color", "red");
      }
    },
    getTakeMedicineTime() {
      if (this.firstTakeMedicineTime == "") {
        this.firstTakeMedicine_Time = this.getTime().timeStampe.getTime();
        this.firstTakeMedicineTime = this.getTime().time;

        sessionStorage.setItem(
          "firstTakeMedicine_Time",
          this.firstTakeMedicineTime
        );
        sessionStorage.getItem("firstTakeMedicine_Time");
      }
      if (this.firstTakeMedicine_Time - this.drawBlood_Time <= 600000) {
        $(".TakeMedicine").css("background-color", "green");
      } else {
        $(".TakeMedicine").css("background-color", "red");
      }
    },
    getTime() {
      let date = new Date();

      let hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
      let minute =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      return { time: hour + ":" + minute, timeStampe: date };
    }
  }
};
