.ivu-layout-sider-children {
  border-right: 1px solid #d8d3d3;
}
.ivu-menu {
  font-size: 20px;
}

// footer
.footer {
  height: 100px;
  line-height: 100px;
  border-top: 1px solid #999;
  float: left;
}
.left {
